//
// Custom form styles
// --------------------------------------------------

.rd-mailform {
  position: relative;
}

// Normalize non-controls
// --------------------------------------------------

label {
  margin-bottom: 10px;
  font-weight: 400;
}

// Common form controls
// --------------------------------------------------

.input-sm,
.input-lg,
.form-control {
  font-size: $input-font-size;

  &, &:focus {
    box-shadow: none;
  }
}

textarea.form-control {
  height: $textarea-default-height;
  min-height: $textarea-default-min-height;
  max-height: $textarea-default-max-height;
  resize: vertical;
}

.form-control {
  //padding-left: 24px;
  //padding-right: 24px;
  border-radius: $input-border-radius-small;
  font-style: italic;
  -webkit-appearance: none;
  line-height: $input-height-base - ($padding-base-vertical + 1px)*2;

  &:focus {
    outline: 0;
  }
}


// Form groups
// -------------------------

.form-group {
  position: relative;
  margin-bottom: 10px;
}

// Form labels
// -------------------------

.form-label {
  position: absolute;
  top: 21px;
  left: $padding-base-horizontal;
  font-size: $input-font-size;
  color: $input-color-placeholder;
  font-style: italic;
  pointer-events: none;
  z-index: 9;
  transition: .3s;
  @include transform(translateY(-50%));

  &.focus {
    opacity: 0;
  }

  &.auto-fill {
    color: $input-color;
  }
}

.form-label-outside {
  @media (min-width: $screen-sm-min) {
    position: static;
    &, &.focus, &.auto-fill {
      @include transform(none);
      color: $input-color-placeholder;
      font-size: $input-font-size;
      opacity: 1;
    }
  }
}

// Form validation
// -------------------------
.form-validation {
  position: absolute;
  right: 10px;
  top: 2px;
  font-size: 11px;
  line-height: 11px;
  color: $brand-danger;
  margin-top: 2px;
  transition: .3s;
  z-index: 11;
}

.form-validation-inside + .form-validation{
  top: 35px;
}

// Error Styling
//
.has-error {
  @include form-control-validation-custom($state-danger-text, $state-danger-bg, $state-danger-bg);
}

// Success Styling
//
.has-success {
  @include form-control-validation-custom($state-success-text, $state-success-bg, $state-success-bg);
}

// Warning Styling
//
.has-warning {
  @include form-control-validation-custom($state-warning-text, $state-warning-bg, $state-warning-bg);
}

// Info Styling
//
.has-info {
  @include form-control-validation-custom($state-info-text, $state-info-bg, $state-info-bg);
}


// Form output
// -------------------------

#form-output-global {
  position: fixed;
  bottom: 30px;
  left: 15px;
  visibility: hidden;
  @include transform(translateX(-500px));
  transition: .3s all ease;
  z-index: 9999999;

  &.active {
    @include transform(translateX(0));
    visibility: visible;
  }

  @media (min-width: $screen-xs) {
    left: 30px;
  }
}

.form-output {
  position: absolute;
  top: 100%;
  left: 0;
  font-size: 14px;
  line-height: 1.5;
  margin-top: 2px;
  transition: .3s;
  opacity: 0;
  visibility: hidden;

  &.active {
    opacity: 1;
    visibility: visible;
  }
  &.error {
    color: $brand-danger;
  }
  &.success {
    color: $brand-success;
  }
}

// Radio and Checkbox Custom
// -------------------------

// Base Styles
//
.radio .radio-custom,
.radio-inline .radio-custom,
.checkbox .checkbox-custom,
.checkbox-inline .checkbox-custom {
  opacity: 0;

  &, &-dummy {
    position: absolute;
    width: 14px;
    height: 14px;
    margin-left: -20px;
    margin-top: 5px;
    outline: none;
    cursor: pointer;
  }

  &-dummy {
    pointer-events: none;
    background: $gray-lighter;
    box-shadow: inset $shadow-area-xxs;

    &:after {
      position: absolute;
      opacity: 0;
    }
  }

  &:focus {
    outline: none;
  }
}

.radio-custom:checked + .radio-custom-dummy:after,
.checkbox-custom:checked + .checkbox-custom-dummy:after {
  opacity: 1;
}

// Custom Radio Styles
//
.radio,
.radio-inline {
  .radio-custom-dummy {
    border-radius: 50%;

    &:after {
      content: '';
      top: 3px;
      right: 3px;
      bottom: 3px;
      left: 3px;
      background: $gray-darker;
      border-radius: 50%;
    }
  }
}

// Custom Checkbox Styles
//
.checkbox,
.checkbox-inline {
  padding-left: 20px;
  .checkbox-custom-dummy {
    pointer-events: none;
    border-radius: $border-radius-small;
    margin-left: 0;
    left: 0;

    &:after {
      content: '\e5ca';
      font-family: 'Material Icons';
      font-size: 20px;
      line-height: 10px;
      position: absolute;
      top: 0;
      left: 0;
      color: $gray-darker;
    }
  }
}

.form-search {
  input.form-control{
    padding-right: 40px;
  }
}
.form-search-submit {
  position: absolute;
  font-size: 22px;
  font-weight: 400;
  right: 13px;
  top: 10px;
  padding: 0;
  background: none;
  border: none;
  box-shadow: none;
  color: #b3b3b3;
  transition: .3s all ease;
}

// subscribe footer
//------------------------------------

.form-subscribe {
  position: relative;
  max-width: 370px;
  .form-control {
    background-color: $gray-darker;
    border-color: $gray;
    @include placeholder($gray);
    padding-right: 50px;
  }

  .btn{
    position: absolute;
    top: -2px;
    right: 15px;
    background-color: transparent;
    border-color: transparent;
    min-width: 20px;
    padding-left: 5px;
    padding-right: 5px;
  }
  .form-validation {
    top: -1px;
    right: 50px;
  }
}

// form inline
//--------------------------------------
@media (min-width: $screen-xs-min) {
  .form-inline-flex-xs {
    @include display-flex;
    @include flex-direction(row);
    @include justify-content(space-between);
    @include align-content(stretch);
    @include align-items(center);
    @include flex-wrap(nowrap);

    .form-group {
      width: 100%;
      margin-bottom: 0;

      & + .form-group {
        margin-left: 20px;
      }
    }
    .btn {
      margin-top: 0;
      margin-left: 14px;
    }
  }
}

.align-bottom {
  @include align-items(flex-end);
}