/*
* @subsection   RD Navbar
*
* @description  Describes style declarations for RD Navbar extension
*
* @author       Evgeniy Gusarov
* @link         https://ua.linkedin.com/pub/evgeniy-gusarov/8a/a40/54a
*/
@import "rd-navbar_includes/_rd-navbar-mixins.scss";

/*
* @subsection General Styles
*/

// RD Navbar Basics
%rd-navbar-transition {
  transition: .3s all cubic-bezier(0.785, 0.135, 0.150, 0.860);
}

%rd-navbar-hidden {
  display: none;
}

%rd-navbar-visible {
  display: block;
}

.rd-navbar--no-transition {
  &, * {
    transition: none !important;
  }
}

.rd-navbar-wrap {
  @extend %rd-navbar-transition;
}

.rd-navbar, .rd-navbar.rd-navbar--is-clone {
  @extend %rd-navbar-hidden;
}

.rd-navbar-fixed,
.rd-navbar-static,
.rd-navbar-fullwidth,
.rd-navbar-sidebar {
  @extend %rd-navbar-visible;
}

.rd-navbar-wrap,
.rd-navbar,
.rd-navbar-brand,
.rd-navbar-slogan,
.rd-navbar-dropdown,
.rd-navbar-megamenu,
.rd-navbar-collapse-items,
.brand-name,
.rd-navbar-nav,
.rd-navbar-panel,
.rd-navbar-search-form-input,
.rd-navbar-search-form-submit,
.rd-navbar-search-toggle,
.rd-navbar-live-search-results,
.rd-navbar-search-form {
  transition: .3s all ease;
}


// RD Navbar Collapse
.rd-navbar-collapse {
  &-toggle {
    @include make-toggle(
            $rd-navbar-collapse-toggle-preset,
            $rd-navbar-min-line-height,
            $rd-navbar-min-font-size,
            $gray-dark
    );
    display: none;
  }
}


// RD Navbar States
.rd-navbar--has-sidebar {
  body {
    padding-left: 270px;
  }
}

.rd-navbar--is-stuck {
  box-shadow: $rd-navbar-shadow;
}

.rd-navbar.rd-navbar-fixed + .rd-navbar.rd-navbar--is-clone,
.rd-navbar.rd-navbar-sidebar + .rd-navbar.rd-navbar--is-clone {
  display: none;
}

@import "rd-navbar_includes/_rd-navbar-components.scss";

/*
* @subsection   Hybrid  Styles
*/

//=========    Static & Fullwidth & Sidebar    ===========
.rd-navbar-static, .rd-navbar-sidebar, .rd-navbar-fullwidth {
  // RD Navbar Search
  .rd-navbar-search {
    &-form {
      &-input {
        input {
          width: 100%;
          padding: 0 10px;
          font-size: 16px;
        }
      }
    }
  }
}

//=============    Static & Fullwidth     ================
.rd-navbar-static,
.rd-navbar-fullwidth {

  &:after {
    content: '';
    background: $rd-navbar-background;
  }

  .rd-navbar-brand,
  .rd-navbar-nav > li > a,
  .rd-navbar-search-toggle {
    position: relative;
    z-index: 2;
  }

  // RD Navbar Submenu
  .rd-navbar-nav {

    > li {
      padding-right: 23px;
      & + li {
        padding-left: 23px;
        border-left: 1px solid #333333;
      }
      > a {
        position: relative;
        display: inline-block;
        color: $rd-navbar-nav-color;
        background: $rd-navbar-nav-background;
        line-height: 1.2;
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
      }

      > .rd-navbar-submenu-toggle {
        display: inline-block;
        cursor: pointer;
        padding-left: 10px;

        &:before {
          content: '\f0d7';
          font-family: 'FontAwesome';
        }
      }
      &.focus > .rd-navbar-submenu-toggle,
      > .rd-navbar-submenu-toggle:hover,
      &.active > .rd-navbar-submenu-toggle {
        color: $rd-navbar-nav-hover-color;
      }

      &.focus > a,
      > a:hover {
        color: $rd-navbar-nav-hover-color;
        background: $rd-navbar-nav-hover-background;
      }

      &.active > a {
        color: $rd-navbar-nav-active-color;
        background: $rd-navbar-nav-active-background;
      }

    }

    > .rd-navbar-submenu {
      > ul {
        box-shadow: $rd-navbar-shadow;
      }

      .rd-navbar-submenu {

        // Submenu Arrow
        &.rd-navbar--has-dropdown {
          position: relative;
        }
      }

      li {
        a{
          line-height: 1.2;
        }
      }


      > .rd-navbar-dropdown,
      > .rd-navbar-megamenu {
        opacity: 0;
        visibility: hidden;
        @include transform(translateY(30px));
        .rd-navbar-submenu {
          position: relative;
        }
        .rd-navbar-submenu-toggle {
          position: absolute;
          top: 55%;
          @include transform(translateY(-50%));
          right: 15px;
          display: inline-block;
          cursor: pointer;
          padding-left: 10px;

          &:before {
            content: '\f0da';
            font-family: 'FontAwesome';
          }
        }
      }

      &.focus {
        > .rd-navbar-dropdown,
        > .rd-navbar-megamenu {
          opacity: 1;
          visibility: visible;
          @include transform(translateY(0));
        }
      }

      .rd-navbar-submenu.focus > .rd-navbar-dropdown {
        display: block;
      }
    }
  }

  // RD Navbar Inner
  .rd-navbar-inner {
    position: relative;
    max-width: $rd-navbar-width;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
  }

  // RD Navbar Nav
  .rd-navbar-nav {
    > li > .rd-navbar-dropdown {
      display: block;
      position: absolute;
      text-align: left;
      margin-top: 22px;
      left: 0;
      width: $rd-navbar-dropdown-width;
      background: $rd-navbar-dropdown-background;
      z-index: 5;
    }

    > li:first-of-type > .rd-navbar-dropdown{
      left: -13px;
      @media (min-width: 1250px) {
        left: -19px;
      }
    }

    li.rd-navbar--has-dropdown {
      position: relative;
    }

    li.focus,
    li.opened {
      > .rd-navbar-dropdown, > .rd-navbar-megamenu {
        opacity: 1;
        visibility: visible;
        @include transform(translateY(0));
      }
    }

    > li {
      display: inline-block;

      > .rd-navbar-dropdown,
      > .rd-navbar-megamenu {
        top: 100%;
        z-index: 15;
      }

      > .rd-navbar-dropdown {

        .rd-navbar-dropdown {
          left: 100%;
          top: 0;
          z-index: 2;
        }
      }
    }
  }

  // RD Navbar Dropdown
  .rd-navbar-dropdown {
    background: $rd-navbar-dropdown-background;

    .rd-navbar-dropdown {
      > li > a {
        color: $white;
      }
    }

    > li > a {
      display: inline-block;
      padding: 12px 20px;
      color: $rd-navbar-dropdown-item-color;
      background: $rd-navbar-dropdown-item-background;
    }

    > li > a:hover {
      color: $rd-navbar-dropdown-item-hover-color;
      background: $rd-navbar-dropdown-item-hover-background;
    }

    > li.focus > a {
      color: $rd-navbar-dropdown-item-active-color;
      background: $rd-navbar-dropdown-item-active-background;
    }
  }

  // RD Navbar Megamenu
  .rd-navbar-megamenu {
    display: table;
    table-layout: fixed;
    left: 10px;
    width: 98%;
    position: absolute;
    text-align: left;
    border-spacing: 31px 25px;
    table-layout: fixed;
    margin-top: 10px;
    max-width: $rd-navbar-width;
    background: $rd-navbar-megamenu-background;
    z-index: 4;

    > li {
      position: relative;
      display: table-cell;

      > p {
        font-weight: 400;
        text-transform: uppercase;
        color: $white;
      }

      > ul {
        padding: 8px 9px;

        li + li {
          margin-top: 5px;
        }

        a {
          display: inline-block;
          color: $rd-navbar-megamenu-item-color;
          background: $rd-navbar-megamenu-item-background;

          &:hover {
            color: $rd-navbar-megamenu-item-hover-color;
            background: $rd-navbar-megamenu-item-hover-background;
          }
        }
      }

      & + li {
        padding-left: 10px;
        &:before {
          content: '';
          position: absolute;
          top: $rd-navbar-megamenu-gutter;
          bottom: $rd-navbar-megamenu-gutter;
          width: 1px;
          right: 100%;
          margin-right: 17px;
          background: lighten($rd-navbar-megamenu-background, 7.450980392156865%);
        }
      }
    }
  }

  // RD Navbar Clone
  &.rd-navbar--is-clone {
    display: none;

    &.rd-navbar--is-stuck {
      display: block;
    }
  }

  // RD Navbar Search
  .rd-navbar-search {
    &-form {
      .form-group {
        margin-bottom: 0;
      }

      &:after {
        left: 100%;
      }

      &-submit {
        background-color: transparent;
        right: 27px;
        border: none;
      }

      &-input {
        position: relative;
        display: inline-block;
        padding-right: 24px;
        padding-left: 10px;
      }
    }

    &-toggle {
      position: absolute;
      right: 50px;
      top: 50%;
      @include transform(translateY(-50%));
      color: $white;
      background-color: transparent;
      border: none;

      &:focus {
        outline: 0;
      }

      &:hover {
        color: $brand-primary;
      }
    }

    &.active {
      .rd-navbar-search-form {
        visibility: visible;
        @include transform(rotateX(0deg));
      }
    }
  }

  // RD Navbar Stuck
  &.rd-navbar--is-stuck,
  &.rd-navbar--is-clone {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 999;
    background: $rd-navbar-background;
    box-shadow: $shadow-area-xs;
  }

  .rd-navbar-dropdown,
  .rd-navbar-megamenu {
    @extend %rd-navbar-transition;
    position: absolute;
    @include transform(translateY(30px));
    padding: 8px 0;
    text-align: left;
    visibility: hidden;
    opacity: 0;
  }

  .rd-navbar-dropdown {
    width: $rd-navbar-dropdown-width;
  }

  // RD Navbar States
  .rd-navbar--has-dropdown {
    position: relative;
  }

  &.rd-navbar--is-clone {

  }
}

//=============      Fixed & Sidebar      ================
.rd-navbar-fixed,
.rd-navbar-sidebar {

  // RD Navbar Collapse
  .rd-navbar-collapse-toggle {
    display: inline-block;
    z-index: 9999;
    position: fixed;
    top: 3px;
    right: 0;
    background-color: transparent;
    border: none;
    outline: none;
  }

  // RD Navbar Dropdown
  .rd-navbar-dropdown {
    display: block;
  }

  .rd-navbar-collapse-items {
    position: absolute;
    width: $rd-navbar-nav-min-width - 10px;
    padding: 25px 15px;
    box-shadow: $rd-navbar-shadow;
    color: $rd-navbar-panel-color;
    background: $rd-navbar-panel-background;
    font-size: 16px;
    line-height: 34px;

    li > * {
      vertical-align: middle;
    }

    li + li {
      margin-top: 10px;
    }

    .icon,
    a {
      display: inline-block;
      font-size: 16px;
      line-height: 30px;
    }

    .icon, a[class*="fa"]:before {
      display: inline-block;
      width: 30px;
      height: 30px;
      padding-right: 5px;
    }
  }
}

.rd-navbar-fixed .rd-navbar-nav,
.rd-navbar-sidebar {
  width: $rd-navbar-nav-min-width;
  height: 100%;
  left: 0;
  top: 0;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  font-size: 16px;
  line-height: 34px;
  color: $rd-navbar-color;
  background: $gray-dark;
  box-shadow: 0 0 11px 2px rgba(0, 0, 0, 0.17);
  z-index: 998;
  padding: 10px 0;

  &:before,
  &:after {
    content: '';
    display: block;
    height: $rd-navbar-min-height;
  }

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background: lighten($rd-navbar-background, 10%);
    border: none;
    border-radius: 0;
    opacity: .2;
  }

  &::-webkit-scrollbar-track {
    background: $rd-navbar-background;
    border: none;
    border-radius: 0;
  }
}

.rd-navbar-fixed .rd-navbar-nav,
.rd-navbar-sidebar .rd-navbar-nav {
  li {
    position: relative;

    li > a {
      padding-left: 20px;
    }

    > a {
      position: relative;
      display: block;
      padding: 5px 45px 5px 15px;

      &:first-letter {
        text-transform: uppercase;
      }
    }

    &.active > a {
      color: $gray-light;
      background: darken($rd-navbar-background, 5%);
    }

    &.focus > a, > a:hover {
      color: $rd-navbar-nav-hover-color;
      background: $rd-navbar-nav-active-background;
    }

    & + li, > img + a, > a + a, > a + ul {
      margin-top: ($rd-navbar-min-height - $rd-navbar-min-line-height) / 2;
    }
  }

  // RD Navbar Dropdown
  .rd-navbar-dropdown {
    > li > a {
      padding-left: 20px;
    }

    ul > li > a {
      padding-left: 25px;
    }
  }
}

.rd-navbar-fixed,
.rd-navbar-sidebar {
  // RD Navbar Search
  .rd-navbar-search {
    &-form {
      &-submit {
        @include transform(scale(.7));
      }
    }
  }
}

@import "rd-navbar_includes/_rd-navbar-static.scss";
@import "rd-navbar_includes/_rd-navbar-fullwidth.scss";
@import "rd-navbar_includes/_rd-navbar-fixed.scss";
@import "rd-navbar_includes/_rd-navbar-sidebar.scss";

/*
* @subsection   Page boxed layout style redeclaration
*
* @description  Redefines navbar style inside boxed layout
*
* @see          ../modules/_page-layouts.scss
*/
html.boxed {
  &.rd-navbar--has-sidebar {
    body {
      padding-left: $rd-navbar-nav-min-width + 30px;
      padding-right: 30px;
    }
  }

  .rd-navbar--is-clone {
    max-width: $layout-boxed-width;
    margin-left: auto;
    margin-right: auto;
  }
}

ul ul, ul ol, ol ul, ol ol {
  padding-left: 0;
}
